import React, { useEffect, useRef, useState, useMemo } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { useWeb3React } from '@web3-react/core/core/core';
import { useTranslation } from 'contexts/Localization';
import { ButtonMenu, ButtonMenuItem, Button, Flex, Text, Box} from '@pancakeswap/uikit';
import ConnectWalletButton from 'components/ConnectWalletButton';
import { ethers } from 'ethers'; 
import { createClient } from '@supabase/supabase-js';
import EmojiPicker from './EmojiPicker';
import { ChatBox, Input, Container, Title,Title2 , Box2} from './StyledComponents';
import NumberPurchaseABI from './NumberPurchaseABI.json';
import contractABI from './contractABI.json';
import GiftABI from './GiftABI.json';
import TokenABI from './TokenABI.json';
import styles from './ChatAppStyles';
import FarmsPoolsRowLoader from '../Home/components/FarmsPoolsRowLoader';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_KEY;
const supabase = createClient(supabaseUrl, supabaseKey);

const Profile: React.FC = () => {
  const { account } = useWeb3React()
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState(''); 
  const [cooldown, setCooldown] = useState(0);
  const chatBoxRef = useRef(null);
  const location = useLocation();
  const [topList, setTopList] = useState([]);
  const [language, setLanguage] = useState('hu'); // Alapértelmezett nyelv: magyar
  const handleLanguageChange = (lang) => {
    setLanguage(lang);
  };
  const [currentPage, setCurrentPage] = useState(1);
  const [names, setNames] = useState({});
  const [userNumbers, setUserNumbers] = useState({});
  const [isSearching, setIsSearching] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [emojiPickerVisible, setEmojiPickerVisible] = useState(false);
  const [image, setImage] = useState(null);
  const [giftBalance, setGiftBalance] = useState(0);
  const [enlargedImage, setEnlargedImage] = useState(null);  // Kép nagyítás állapota
  const [contract, setContract] = useState<ethers.Contract | null>(null);
  const [contract2, setContract2] = useState<ethers.Contract | null>(null);
  const [giftContract, setGiftContract] = useState<ethers.Contract | null>(null);
  const [tokenContract, setTokenContract] = useState<ethers.Contract | null>(null);
  const itemsPerPage = 25;
  const { t } = useTranslation();
  const [displayedMessages, setDisplayedMessages] = useState(20);
  const [isTopListVisible, setIsTopListVisible] = useState(false);
  const [userCooldown, setUserCooldown] = useState(0);  // Felhasználó egyéni cooldown
const [globalCooldown, setGlobalCooldown] = useState(0);  // Globális cooldown
  const [currentCollectionIndex, setCurrentCollectionIndex] = useState(0);
const [currentCollectionAmount, setCurrentCollectionAmount] = useState(0);
  const contractAddress = '0x674e3eDc898Aa4b486Bd2E5aa2D1C8653677F471';  
  const contractAddress2 = '0xBE8C7E554C89e064ED7229952036c3678cd10180';
  const giftAddress = '0x051A79ECC8736b448C1162162F3BB87a6a41F7d4';  
  const tokenContractAddress = '0xaDAaE082237cB1772c9e079dB95c117E6Dd0C5aF';
  const fileInputRef = useRef(null);

  useEffect(() => {
    let previousCollectionAmount = null;
    let previousCollectionIndex = null;
  
    const fetchCurrentCollectionInfo = async () => {
      if (!giftContract) {
        console.log("Gift contract is not initialized.");
        return;
      }
  
      try {
        // Lekérjük az aktuális begyűjtési információkat
        const [index, amount] = await giftContract.getCurrentCollectionInfo();
        const formattedAmount = parseFloat(ethers.utils.formatUnits(amount, 18));
  
        // Ha 0-t kapunk vissza, ne frissítsük az állapotot
        if (formattedAmount === 0) {
          console.log('Skipping: amount is zero.');
          return;
        }
  
        // Csak akkor frissítsük az állapotot, ha az új érték eltér a korábbitól
        if (previousCollectionIndex !== index.toString() || previousCollectionAmount !== formattedAmount) {
          // Frissítjük a local állapotot
          setCurrentCollectionIndex(parseInt(index.toString(), 10));
          setCurrentCollectionAmount(formattedAmount);
  
          // Frissítjük a korábbi értékeket
          previousCollectionIndex = index.toString();
          previousCollectionAmount = formattedAmount;
        } else {
          console.log('No new collection info to update.');
        }
      } catch (error) {
        console.error('Error fetching current collection info:', error);
      }
    };
  
    // Ismételt lekérdezés 5 másodpercenként
    const interval = setInterval(() => {
      fetchCurrentCollectionInfo();
    }, 5000);
  
    // Tisztítás az unmountnál
    return () => clearInterval(interval);
  }, [giftContract]);  
    
  
  useEffect(() => {
    const fetchCooldownTimes = async () => {
      if (!giftContract || !account) return;
  
      try {
        // Lekérdezzük a felhasználó egyéni cooldown idejét
        const userCooldownTime = await giftContract.getUserCooldownTime(account);
        setUserCooldown(userCooldownTime.toString());
  
        // Lekérdezzük a globális cooldown idejét
        const globalCooldownTime = await giftContract.getGlobalCooldownTime();
        setGlobalCooldown(globalCooldownTime.toString());
      } catch (error) {
        console.error("Error fetching cooldown times:", error);
      }
    };
  
    fetchCooldownTimes();
  
    const interval = setInterval(fetchCooldownTimes, 1000); // 1 másodpercenként frissítjük
  
    return () => clearInterval(interval);
  }, [giftContract, account]);
  
  // Idő formázása HH:MM:SS formátumra
  const formatTime = (seconds) => {
    const h = Math.floor(seconds / 3600);
    const m = Math.floor((seconds % 3600) / 60);
    const s = seconds % 60;
    return [h, m, s].map(v => String(v).padStart(2, '0')).join(':');
  };

  const handleImageClick = (src) => {
    setEnlargedImage(src);  // Kép forrásának tárolása a modalhoz
  };
  
  const handleModalClose = () => {
    setEnlargedImage(null);  // Modal bezárása
  };
  
  const handleSendImage = (imageSrc) => {
    setImage(imageSrc); // Set the selected image for sending
  };

  useEffect(() => {
    const initializeContracts = async () => {
        const provider = new ethers.providers.JsonRpcProvider('https://bsc-dataseed1.binance.org');
        
        // Contractok inicializálása a provider-rel
        setContract(new ethers.Contract(contractAddress, contractABI, provider));
        setContract2(new ethers.Contract(contractAddress2, NumberPurchaseABI, provider));
        setGiftContract(new ethers.Contract(giftAddress, GiftABI, provider));
        setTokenContract(new ethers.Contract(tokenContractAddress, TokenABI, provider));
    };
    
    initializeContracts();
}, []);

  
  const handleScroll = async (e) => {
    const chatBox = chatBoxRef.current;
  
    // Ha már betöltöttük a 100 üzenetet, ne töltsön be többet
    if (displayedMessages >= 100) {
      return;
    }
  
    // Check if the user has scrolled to the top
    if (chatBox.scrollTop === 0 && displayedMessages < Math.min(100, messages.length)) {
      const currentScrollHeight = chatBox.scrollHeight;
      const oldestTimestamp = messages[0]?.timestamp;
  
      const { data, error } = await supabase
        .from('messages')
        .select('*')
        .order('timestamp', { ascending: false })
        .lt('timestamp', oldestTimestamp)
        .limit(20); // Load 20 more messages
  
      if (error) {
        console.error('Error fetching older messages:', error);
      } else {
        setMessages((prevMessages) => [...data.reverse(), ...prevMessages].slice(-100)); // Keep only the latest 60 messages
        setDisplayedMessages((prev) => Math.min(prev + 20, Math.min(100, messages.length)));
  
        setTimeout(() => {
          const newScrollHeight = chatBox.scrollHeight;
          chatBox.scrollTop = newScrollHeight - currentScrollHeight;
        }, 0);
      }
    }
  };  
   
  
  const handleDeleteMessage = async (msg) => {
    if (!window.confirm('Are you sure you want to delete this message?')) {
      return;
    }
  
    try {
      const { error } = await supabase
        .from('messages')
        .delete()
        .eq('id', msg.id) // Feltételezve, hogy az üzenetnek van egyedi azonosítója
        .eq('sender', account); // Csak a saját üzenetedet tudod törölni
  
      if (error) {
        console.error('Error deleting message:', error.message);
        return;
      }
  
      // Töröljük az üzenetet a helyi állapotból is
      setMessages(prevMessages => prevMessages.filter(m => m.id !== msg.id));
    } catch (error) {
      console.error('Unexpected error deleting message:', error);
    }
  };
  

  const collectTokens = async () => {
    try {
      const tx = await giftContract.collect();
      await tx.wait();
      alert('Tokens collected successfully!');
  
      // Gyűjtés után frissítsük a cooldown időket
      const userCooldownTime = await giftContract.getUserCooldownTime(account);
      const globalCooldownTime = await giftContract.getGlobalCooldownTime();
      
      setUserCooldown(userCooldownTime.toString());
      setGlobalCooldown(globalCooldownTime.toString());
  
      // A többi adat frissítése
      const lastCollector = await giftContract.lastCollector();
      const lastCollectedAmount = await giftContract.lastCollectedAmount();
      const formattedAmount = ethers.utils.formatUnits(lastCollectedAmount, 18);
    } catch (error) {
      console.error('Error collecting tokens:', error);
      alert('Failed to collect tokens');
    }
  };
  

  useEffect(() => {
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  }, [account, messages,language]); 

  useEffect(() => {
    const fetchMessages = async () => {
      const { data, error } = await supabase
        .from('messages')
        .select('*')
        .order('timestamp', { ascending: false })
        .limit(100); // Limit the initial fetch to 100 messages
    
      if (error) {
        console.error('Error fetching messages:', error);
      } else {
        setMessages(data.reverse().slice(-100));  // Limit to 100 messages
      }
    };
  
    fetchMessages();
  }, []);
  
  
  useEffect(() => {
    const channel = supabase
      .channel('public:messages')
      .on('postgres_changes', { event: 'INSERT', schema: 'public', table: 'messages' }, payload => {
        const newMessage = payload.new;
        setMessages(prevMessages => {
          const updatedMessages = [...prevMessages, newMessage];
          return updatedMessages;
        });
      })
      .subscribe();
    
    return () => {
      supabase.removeChannel(channel);
    };
  }, []);
  
  const userList = useMemo(() => {
    const usersFromMessages = messages.map(msg => msg.sender);
    const usersFromTopList = topList.map(([user]) => user);
    return Array.from(new Set([...usersFromMessages, ...usersFromTopList])); // Egyedi felhasználók összegyűjtése
  }, [messages, topList]);

  useEffect(() => {
    if (!account || userList.length === 0) return;
    
    const fetchUserData = async () => {
      const newNames = {};
      const newNumbers = {};
    
      await Promise.all(userList.map(async (user) => {
        try {
          const { data: existingData, error: fetchError } = await supabase
            .from('user_data')
            .select('*')
            .eq('account', user)
            .single();
    
          if (fetchError && fetchError.code !== 'PGRST116') {
            console.error(`Error fetching data from Supabase for ${user}:`, fetchError);
            return;
          }
    
          if (existingData) {
            newNames[user] = existingData.name;
            newNumbers[user] = existingData.number;
          } else {
            const name = await contract.getName(user);
            const userPurchase = await contract2.userPurchases(user);
            const userNumber = userPurchase.number.toNumber();
    
            newNames[user] = name;
            newNumbers[user] = userNumber;
    
            const { error } = await supabase.from('user_data').insert([{ account: user, name, number: userNumber }]);
            if (error) {
              console.error(`Error inserting data to Supabase for ${user}:`, error);
            } else {
              console.log(`User data inserted to Supabase for ${user} successfully`);
            }
          }
        } catch (error) {
          console.error(`Error fetching data for ${user}:`, error);
        }
      }));
    
      setNames(newNames);
      setUserNumbers(newNumbers);
    };
    
    fetchUserData();
  }, [account, userList, contract, contract2]);
    
  useEffect(() => {
    if (cooldown > 0) {
      const timer = setTimeout(() => setCooldown(cooldown - 1), 1000);
      return () => clearTimeout(timer);
    }
    return undefined; // Hozzáadunk egy visszatérési értéket akkor is, ha cooldown <= 0
  }, [cooldown]);
  
  const toggleSearchMode = () => {
    setIsSearching(!isSearching);
    setSearchQuery('');
  };

  const handleSearchInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  const handleImageChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
    
      if (file.size > 1048576) {
        alert('The file size must be less than 1 MB.');
        return;
      }
    
      const reader = new FileReader();
      reader.onload = (e) => {
        setImage(e.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSend = async () => {
    if (cooldown > 0 || !account) return;  // Ensure there is a logged-in user
  
    // If there's no text but there is an image, add a zero-width space
    const messageText = newMessage.trim() || (image ? '\u200B' : '');
  
    if (!messageText && !image) return;  // Ensure that we have either text or an image
   
    const message = {
      text: messageText,  // Use the modified messageText
      sender: account,
      image,
      language, // Hozzáadjuk a nyelvet az üzenethez
    };
  
    const { data, error } = await supabase
      .from('messages')
      .insert([message]);
  
    if (error) {
      console.error('Error sending message:', error);
      alert(`Failed to send message: ${error.message}`);
    } else {
      setNewMessage('');
      setImage(null);
      setCooldown(20);
    }
  };

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const getSenderName = (sender) => {
    if (!sender) {
      return 'Unknown';
    }
    
    if (sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1') {
      return 'Admin';
    }
    
    return names[sender] || `${sender.substring(0, 5)}...${sender.substring(sender.length - 5)}`;
  };
  
  const getSenderStyle = (sender) => {
    // Admin és másik meghatározott cím arany színnel
    if (
      sender === '0xEa9C8f2D9d91cF44206F0d869b9cc39897a13Ae1' || // Admin cím
      sender === '0x8eda75a5669109492F8871235970CB066eE1722d'      // Új arany cím
    ) {
      return { color: 'gold' };
    }
    
    // Minden más felhasználónak ezüst szín
    return { color: '#E5E4E2' };
  };

  const getSenderImage = (sender) => {
    const userNumber = userNumbers[sender];
    
    if (userNumber) {
      let formattedNumber;
  
      if (userNumber < 10) {
        // Kétjegyűre formázzuk, ha a szám kisebb, mint 10
        formattedNumber = userNumber.toString().padStart(2, '0');
      } else {
        // Háromjegyűre formázzuk, ha a szám 10 vagy nagyobb
        formattedNumber = userNumber.toString().padStart(3, '0');
      }
  
      return `images/pixel/${formattedNumber}.jpg`;
    }
  
    return 'images/pixel/200.png';  // Alapértelmezett kép, ha nincs szám
  };
  

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp); // Eredeti UTC idő
    const timeString = date.toLocaleTimeString(navigator.language, {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false,
    });
    const dateString = date.toLocaleDateString(navigator.language, {
      month: 'long',     // Hónap neve
      day: 'numeric',    // Nap
    });
    return `${timeString}, ${dateString}`;
  };
  
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentItems = topList.slice(startIndex, endIndex);

  const totalPages = Math.ceil(topList.length / itemsPerPage);

  const getPageNumbers = () => {
    const pageNumbers = [];
    const maxPageNumbers = 3;
    const startPage = currentPage;

    for (let i = 0; i < maxPageNumbers && startPage + i <= totalPages; i++) {
      pageNumbers.push(startPage + i);
    }

    return pageNumbers;
  };
  
  const getActiveIndex = (pathname) => {
    if (
      pathname.includes('/lockedstaking/all') ||
      pathname.includes('/lockedstaking/history')
    ) {
      return 1;
    }
    return 0;
  };

  const filteredMessages = messages.filter((msg) => msg.language === language);

  return (
    <Container>
  
                    <FarmsPoolsRowLoader />
          <Title>VizslaChat</Title>
          <Flex alignItems="center" justifyContent="center" >
            <ButtonMenu activeIndex={getActiveIndex(location.pathname)} scale="sm" variant="subtle">
              <ButtonMenuItem id="swap-nav-link" to="/chat" as={Link}>
                {t('Public')}
              </ButtonMenuItem>
              <ButtonMenuItem id="swap-nav-link" to="/privatechat" as={Link} mt="4px">
                {t('Private')}
              </ButtonMenuItem> 
              <ButtonMenuItem id="swap-nav-link" to="/settings" as={Link} mt="4px">
                {t('Settings')}
              </ButtonMenuItem> 
            </ButtonMenu>
          </Flex>
          <Flex alignItems="center" justifyContent="center" mt="15px">
          <ButtonMenu activeIndex={language === 'hu' ? 0 : 1} onItemClick={(index) => handleLanguageChange(index === 0 ? 'hu' : 'en')} scale="sm" variant="subtle">
  <ButtonMenuItem>
    <img src="images/pixel/777.png" alt="Hungarian Flag" style={{ width: '20px', marginRight: '8px' }} />
    HU
  </ButtonMenuItem>
  <ButtonMenuItem>
    <img src="images/pixel/776.png" alt="English Flag" style={{ width: '20px', marginRight: '8px' }} />
    EN
  </ButtonMenuItem>
</ButtonMenu>
</Flex>
          {account ? (
        <>
        <ChatBox ref={chatBoxRef} onScroll={handleScroll}>
  {filteredMessages.slice(-displayedMessages).map((msg) => (
    <div key={msg.id} style={styles.messageContainer}>
      {/* Feladó képe */}
      <img src={getSenderImage(msg.sender)} alt="sender" style={styles.avatar} />
      {/* Üzenet szövege és szemetes ikon flexbox-szal elrendezve */}
      <div style={{ ...styles.message, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        {/* Bal oldalon az üzenet és az időbélyeg */}
        <div>
          <b style={getSenderStyle(msg.sender)}>{getSenderName(msg.sender)}:</b> {msg.text}
          {msg.image && (
            <Button
              style={{ background: 'none', border: 'none', padding: '0', cursor: 'pointer' }}
              onClick={() => handleImageClick(msg.image)}
              onKeyDown={(e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                  handleImageClick(msg.image);
                }
              }}
            >
              <img
                src={msg.image}
                alt="sent"
                style={{
                  maxWidth: '100px',
                  maxHeight: '100px',
                  objectFit: 'contain',
                  marginTop: '40px',
                }}
              />
            </Button>
          )}
          {/* Timestamp az üzenet alatt */}
          <span style={styles.timestamp}>{formatTimestamp(msg.timestamp)}</span>
        </div>
        {/* Szemetes ikon csak a saját üzenetekhez */}
        {msg.sender.toLowerCase() === account.toLowerCase() && (
          <Button
            onClick={() => handleDeleteMessage(msg)}
            style={{
              background: 'none',
              border: 'none',
              cursor: 'pointer',
              padding: '0',
              marginLeft: '10px', // Kis térköz a szemetes ikon előtt
              fontSize: styles.timestamp.fontSize, // Ugyanakkora méretű lesz, mint a timestamp
              display: 'inline-flex', // Inline marad a szöveggel
              alignItems: 'center', // Középre igazítás
            }}
          >
            🗑️
          </Button>
        )}
      </div>
    </div>
  ))}
</ChatBox>

   
          <div style={styles.inputRow}>
            <Flex alignItems="center" justifyContent="center">
              <Input
                type="text"
                value={isSearching ? searchQuery : newMessage}
                onChange={isSearching ? handleSearchInputChange : (e) => setNewMessage(e.target.value)}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !isSearching && cooldown === 0) {
                    handleSend();
                  }
                }}
                style={styles.input}
                placeholder={isSearching ? 'Search in chatbox...' : (cooldown > 0 ? `Cooldown: ${cooldown}s` : 'Type a message')}
                disabled={cooldown > 0 && !isSearching}
              />
              <Button onClick={handleSend} style={styles.sendButton} disabled={cooldown > 0}>
                Send
              </Button>
              <Button onClick={() => setEmojiPickerVisible(!emojiPickerVisible)} style={styles.emojiButton}>
                😊
              </Button>
              <Button 
                onClick={() => fileInputRef.current.click()} 
                style={styles.fileButton}>
                📤
              </Button>
              <Link to="/toplist" >
    <Button variant='gold' ml="5px" width="15px">
      🏆 {/* Trófea ikon */}
    </Button>
  </Link> 
            </Flex>
            {image && (
  <div style={styles.previewContainer}> 
    <Text style={styles.previewText}>Image selected:</Text>
    <img src={image} alt="Preview" style={styles.previewImage} />
    <Button onClick={() => setImage(null)} style={styles.deleteButton}>
      ❌ 
    </Button>
  </div>
)}

          </div>
  
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            style={{ display: 'none' }}
            onChange={handleImageChange}
          />

          {emojiPickerVisible && (
            <EmojiPicker 
              setNewMessage={setNewMessage} 
              newMessage={newMessage} 
              handleSendImage={handleSendImage}  // Pass handleSendImage
            />
          )}
 <Flex alignItems="center" justifyContent="center">
  <Box
    color="#000" 
    p="24px" 
    background="black" 
    borderRadius="12px" 
    borderColor="primary" 
    border="2px solid"  // Itt definiáljuk az egész border-t
    mt="50px" 
    width="100%" 
    >
    {/* Chat Rewards Title */}
    <Text bold fontSize="32px" color="gold" textAlign="center" mb="6px">
    ⚡Flash Rewards⚡
    </Text>

    {/* Current Round and Tokens */}
    <Text  fontSize="22px" color="#fff" mb="6px" bold textAlign="center">
      ROUND {currentCollectionIndex}/10
    </Text>
    <Text bold fontSize="28px" color="primary" mb="6px" textAlign="center">
      {currentCollectionAmount} VIZSLASWAP
    </Text> 
 
    {/* Next Round Reward */}
    <Text  fontSize="18px" color="#fff" mb="8px" bold textAlign="center">
      NEXT ROUND:
      <Text  color="blue.300"  fontSize="18px" ml="8px" display="inline-flex" alignItems="center">
        {currentCollectionIndex < 9 ? `${[200, 220, 180, 230, 170, 240, 160, 250, 1, 373][currentCollectionIndex]} VIZSLASWAP` : 'End of Round'}
      </Text> 
    </Text>

    {/* Collect Tokens Button */}
    <Flex alignItems="center" justifyContent="center">
    <Button 
      variant="gold" 
      onClick={collectTokens} 
      width="200px" 
      mt="12px" 
    >
      Collect Tokens  
    </Button>
    </Flex>
    <Text bold fontSize="12px" color="gold" textAlign="center" mt="4px">
      Available only for users with a profile picture!
    </Text>
    {/* Cooldown Info */}
    <Flex flexDirection="column" alignItems="center"  mt="24px">
      <Text bold fontSize="18px" color="#fff" mb="8px">
        Your Cooldown:
        <Text as="span" color={userCooldown > 0 ? "red.400" : "green.400"} ml="8px">
          {userCooldown > 0 ? formatTime(userCooldown) : 'NOW'}
        </Text>
      </Text>
      <Text bold fontSize="18px" color="#fff">
        Global Cooldown:
        <Text as="span" color={globalCooldown > 0 ? "red.400" : "green.400"} ml="8px">
          {globalCooldown > 0 ? formatTime(globalCooldown) : '0:00:00'}
        </Text>
      </Text>
    </Flex>
      {/* Profile Picture Warning */}
     
  </Box>
</Flex>

       
{enlargedImage && (
  <div
    style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.8)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000,
    }}
    onClick={handleModalClose}
    role="button"
    tabIndex="0"
    onKeyDown={(e) => {
      if (e.key === 'Enter' || e.key === ' ') {
        handleModalClose();
      }
    }}
  >
    <img
      src={enlargedImage}
      alt="Enlarged"
      style={{ maxWidth: '90%', maxHeight: '90%', cursor: 'pointer' }}
    />
  </div>
)}

        </>
      ) : (
        <ConnectWalletButton />
      )}
    </Container>
  );
};

export default Profile;
